#root, html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: black;
}

.full-screen {
  height: 100%;
  width: 100%;
}

.full-width {
  width: 100%;
}

.scrollable-x {
  overflow-x: scroll;
}

.fixed {
  position: fixed;
}

iframe {
  width: 100%;
  height: 100%;
}

.iframe-full-screen {
  height: 92%;
}

@media screen and (orientation:landscape) {
  .iframe-full-screen {
    height: 85%;
  }
}